.expect__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    grid-gap: 1.5rem;
    &-single {
        justify-self: center;
        width: 100%;
        padding: 1.5rem;
        border: 1px solid rgba(0,0,0,0.10);
        border-radius: 10px;
        box-sizing: border-box;
        &-icon {
            max-width: 5rem;
            max-height: 5rem;
            display: inline-block;
            margin-bottom: 1rem;
            width: 100%;
            height: 100%;
            & img, & svg {
                width: 100%;
                height: 100%;
                object-fit: contain;
                display: block;
            }
            
        }
        &-title {
            & h3 {
                margin: 0;
                font-size: 1.3rem;
                font-weight: 500;
            }
        }
    }    
}

@media screen and (max-width: 767px) {
    .expect__list {
        grid-template-columns: repeat(2, 1fr);
        padding: 0 30px;     
    }
}